import $ from 'jquery';
import hyperform from 'hyperform';

const form = (() => {
	let nextBtn = $('.form__btn-next');
	nextBtn.on('click', function(e){
		e.preventDefault();
		const parent = $(this).closest('.form__step');

		
		parent.removeClass('form__step_active');
		parent.next().addClass('form__step_active');
	});
})();

export default form


